// NOTE: docs
// https://leaverou.github.io/awesomplete/#extensibility
import { config } from '../config';
import { storageBase } from '../storage/storage-base';
import { lStopsRaw } from '../data/l-stops';
import { arrivalsStorage } from '../storage/arrivals-storage';
import { settingsStorage } from '../storage/settings-storage';
import { helpers } from '../helpers';

const { lines, commutes } = config;
const { CtaStorage } = storageBase;
const { reload } = helpers;

function setAwesomplete(input, amPm, opts = {}) {
  new Awesomplete(input, {
    minChars: 1,
    maxItems: 20,
    autoFirst: true,
    list: lStopsRaw,
    data: function (item, input) {
      // NOTE: this is confusingly named
      // STOP_ID has direction
      // MAP_ID does not (both ways) is really a 'station'

      const activeLines = [];
      $(lines).each(function (i, line) {
        if (item[line] === 1) {
          activeLines.push(line);
        }
      });
      return {
        label: item.STOP_NAME,
        value: {
          name: item.STOP_NAME,
          mapId: item.STOP_ID,
          stationid: item.MAP_ID,
          activeLines: activeLines,
          stationName: item.STATION_NAME,
        },
      };
    },
    replace: function (text) {
      this.input.value = text.label;
      settingsStorage.clearFilterCache(amPm);
    },
  });
}

$().ready(function () {
  function unhideCommuteIfNotSet(amPm, key) {
    if (CtaStorage.getItem(key) === null) {
      $('#am-station-search')[0].placeholder = 'Select AM Station';
      $('#pm-station-search')[0].placeholder = 'Select PM Station';
      $('#configure-commute').removeClass('hidden');
      $(`.all-data-${amPm}`).removeClass('hidden');
      $(`.trainData-${amPm}`).addClass('hidden');
    } else if (!arrivalsStorage.currentCommuteTypeNotSet(amPm)) {
      $('.switch.switch-horizontal').removeClass('hidden');
    }
  }

  function configureDestinationStationChanger(amPm) {
    const input = document.getElementById(`${amPm}-destination-station`);
    const key = `${amPm}-destination-station`;
    setAwesomplete(input, amPm);
    Awesomplete.$.bind(input, { 'awesomplete-selectcomplete': setDestinationStation });

    function setDestinationStation(e) {
      const label = e.text.label;
      const values = e.text.value;
      $(`span.${key}-label`).empty();
      $(`span.${key}-label`).text(label);

      const storedStationInfo = {
        mapId: values.mapId,
        stationid: values.stationid,
        stopName: label,
        activeLines: values.activeLines,
        stationName: values.stationName,
      };
      CtaStorage.setItem(key, JSON.stringify(storedStationInfo));
      arrivalsStorage.clearOldTrainArrivalData();
      reload({ delay: 0 });
    }
  }

  function configureStationChanger(amPm) {
    const input = document.getElementById(`${amPm}-station-search`);
    const key = `${amPm}-commute-station`;
    const destinationKey = `${helpers.oppositeCommute(amPm)}-destination-station`;
    unhideCommuteIfNotSet(amPm, key);

    setAwesomplete(input, amPm);
    Awesomplete.$.bind(input, { 'awesomplete-selectcomplete': setDepartureStation });

    function setDepartureStation(e) {
      const label = e.text.label;
      const values = e.text.value;
      $(`span.${key}-label`).empty();
      $(`span.${key}-label`).text(label);

      const storedStationInfo = {
        mapId: values.mapId,
        stationid: values.stationid,
        stopName: label,
        activeLines: values.activeLines,
        stationName: values.stationName,
      };
      CtaStorage.setItem(key, JSON.stringify(storedStationInfo));

      // NOTE: sets the destination default
      CtaStorage.setItem(destinationKey, JSON.stringify(storedStationInfo));
      arrivalsStorage.clearOldTrainArrivalData();
      reload({ delay: 0 });
    }
  }

  $(commutes).each(function(i, amPm) {
    configureStationChanger(amPm);
    configureDestinationStationChanger(amPm);
  });
});
