import { config } from '../config';
import { helpers } from '../helpers';
import { followersStorage } from '../storage/followers-storage';
import { arrivalsStorage } from '../storage/arrivals-storage';
import { timeHelper } from '../views/time-helper';
import { followersHelper } from '../views/followers-helper';

const { proxyUrl, apiKey } = config;
const followerUrl = 'https://lapi.transitchicago.com/api/1.0/ttfollow.aspx';

function followerQueryUrl(runNum) {
  return `${proxyUrl}${followerUrl}?key=${apiKey}&runnumber=${runNum}&outputType=JSON`;
}

function isDestinationStation(stopId1, stopId2) {
  return parseInt(stopId1, 10) === parseInt(stopId2, 10);
}

function lacksRunData(runData) {
  return runData.errCd === '502';
}

function ridingSameTrain(runNum) {
  return $('.boarded-run-number').data('runNumber') === runNum;
}

function ridingTrain() {
  return $('.boarded-wrapper').is(':visible');
}

function confirmBoardText(runNum, lineName) {
  if (ridingSameTrain(runNum)) {
    return `You are already riding CTA ${lineName} run #${runNum}.`;
  } else if (ridingTrain()) {
    return `You are currently riding ${$('.boarded-run-number').data('lineName')} #${$('.boarded-run-number').data('runNumber')}, switch trains and board ${lineName} #${runNum} instead?`;
  } else {
    return `Board CTA ${lineName} run #${runNum}?`;
  }
}

function detrain(timer) {
  $('.boarded-run-number').removeData('runNumber');
  followersStorage.clearCurrentRideKey();
  clearInterval(timer);
}

function setupBoardingClickHandlers(timer, runNum, lineName) {
  $('img.boarding-close-icon').off('click');
  $('img.boarding-close-icon').click(function () {

    if (confirm(`Are you sure you want to get off ${lineName} train #${runNum}?`)) {
      $('.boarded-wrapper').addClass('hidden');
      detrain(timer);
    }
  });
}

const followersData = {
  destinationStation: function() {
    return arrivalsStorage.fetchDestinationStationDataFor(helpers.selectedCommute());
  },


  // start a checking polling every 20 seconds to see if has arrival data
  // top bar

  hasRecentRide: function () {
    // Steps to determine if active current ride:
    // 1) followersStorage.currentRide
    // 2) check if time isn't more than 2 hours in past
    // 3)
    //    a if is less than 20 mins old
    //      check followers and see if has predictions
    //    b is is older, prompt user if they're still on the train? maybe gets annoying

    // use eta data from before last refresh if possible

    if (followersStorage.hasCurrentRide() && followersStorage.currentRideStale()) {
      followersStorage.clearCurrentRideKey();
    } else if (followersStorage.hasCurrentRide()) {
      const currentRide = followersStorage.currentRide();

      $('.boarded-line-name').text(currentRide.lineName);
      $('.boarded-run-number').text(`#${currentRide.runNum}`);
      $('.boarded-destination-station').text(currentRide.destinationName);
      $('.boarded-run-number').data('runNumber', currentRide.runNum);
      $('.boarded-run-number').data('lineName', currentRide.lineName);

      if (followersStorage.arrived()) {
        $('.eta-boarded').text(currentRide.lastEta);
        followersHelper.setArrived();
      } else {
        followersHelper.showRideBanner();
        $('.eta-boarded').text(`ETA: ${currentRide.lastEta}`);
      }
      const timer = this.followerDataStartPoll();
      setupBoardingClickHandlers(timer, currentRide.runNum, currentRide.lineName);
    }
  },

  followerDataStartPoll: function () {
    const that = this;
    that.runData = followersStorage.currentRide().runNum;
    const interval = setInterval((function () {
      function timer() {
        $.ajax({
          url: followerQueryUrl(that.runData),
          type: 'GET',
          async: true,
        }).done(function (response) {
          const runData = response.ctatt;

          // flash the pinging for updated eta data
          // maybe: https://tilomitra.github.io/infinite/


          // NOTE: also updates eta in localstorage

          const newEta = that.getEta(runData);
          $('span.eta-boarded').text(newEta);

        }).fail(function (data, textStatus, xhr) {
          console.log(data);
          console.log(textStatus);
          console.log(xhr);
        });
      }
      timer();
      return timer;
    }()), 60000); // NOTE: 60 seconds
    return interval;
  },

  hasPredictionDataForStop: function(runData) {
    const that = this;
    that.hasEta = false;
    $(runData.eta).each(function (i, eta) {
      if (isDestinationStation(that.destinationStation().stationid, parseInt(eta.staId, 10))) {
        that.hasEta = true;
      }
    });
    return that.hasEta;
  },

  confirmBoard: function(runNum, lineName, runData) {
    if (confirm(confirmBoardText(runNum, lineName))) {
      // NOTE get off any trains you're already on

      if (ridingSameTrain(runNum)) {
        return;
      }
      detrain(window.CTAcurrentRideTimer);
      // TODO: down the line, allow station adjust to get different etas here:
      // for now hardcoded

      const eta = this.getEta(runData);
      $('.eta-boarded').text(eta);
      $('.boarded-destination-station').text(this.destinationStation().stationName);
      $('.boarded-line-name').text(lineName);
      $('.boarded-run-number').text(`#${runNum}`);
      $('.boarded-run-number').data('runNumber', runNum);
      $('.boarded-run-number').data('lineName', lineName);

      followersStorage.setCurrentRide({
        destinationStation: this.destinationStation().stationid,
        runData: runData,
        runNum: runNum,
        boardedAt: helpers.now(),
        lineName: lineName,
        destinationName: this.destinationStation().stationName,
      });
      followersHelper.showRideBanner();
      const timer = this.followerDataStartPoll(); // NOTE: start polling for updated arrivals
      window.CTAcurrentRideTimer = timer;
      setupBoardingClickHandlers(timer, runNum, lineName);
    }
  },

  getEta: function(runData) {
    if (lacksRunData(runData)) {
      return 'ETA: unknown';
    }

    if (this.hasPredictionDataForStop(runData)) {
      const that = this;
      that.estEta = undefined;
      $(runData.eta).each(function (i, eta) {
        if (isDestinationStation(that.destinationStation().stationid, parseInt(eta.staId, 10))) {
          that.estEta = timeHelper.printTime(eta.arrT);
          if (followersStorage.hasCurrentRide()) {
            followersStorage.updateCurrentRide('lastEtaRaw', timeHelper.parseCtaTime(eta.arrT).getTime());
            followersStorage.updateCurrentRide('lastEta', timeHelper.printTime(eta.arrT));
          }
        }
      });
      followersHelper.showRideBanner();
      return `ETA: ${that.estEta}`;
    } else if (followersStorage.hasCurrentRide()) {
      const lastEtaRaw = followersStorage.lastEtaRaw();
      // TODO: start here, get the prediction - check if far out
      // if far out don't set arrived.

      if (lastEtaRaw === 'unknown' || lastEtaRaw > helpers.now()) {
        // NOTE: do nothing
        return;
      } else {
        followersStorage.updateCurrentRide('lastEta', 'ARRIVED');
        followersStorage.updateCurrentRide('lastEtaRaw', 'ARRIVED');
        followersHelper.setArrived();
        return 'ARRIVED';
      }
    }
  },

  boardTrain: function(runNum, lineName) {
    const that = this;
    that.runNum = runNum;
    that.lineName = lineName;
    $.ajax({
      url: followerQueryUrl(runNum),
      type: 'GET',
      async: true,
    }).done(function(response) {
      const runData = response.ctatt;
      that.confirmBoard(that.runNum, that.lineName, runData);
      if (ridingSameTrain(runNum)) {
        return;
      }
      if (lacksRunData(runData)) {
        // NOTE: not all trains have data
        // OR it is sporatically available
        return;
      }

      // Set boarded state
      // periodically check to see if it does have follower data for your desintation station
      // after is arrived clear the boarded status
      this.hasPredictionDataForStop(runData);
    }).fail(function (data, textStatus, xhr) {
      console.log(data);
      console.log(textStatus);
      console.log(xhr);
    });
  },
};

export { followersData };
