function formatClockOutput(digits) {
  // NOTE: always show two digits
  return digits < 10 ? `0${digits}` : digits;
}

function clockOutput(min, sec) {
  return `${formatClockOutput(min)}:${formatClockOutput(sec)}`;
}

function displayCountdown(isDelay, min, sec) {
  if (isDelay === true) {
    if (min === 0 && sec === 0) {
      return clockOutput(min, sec)
    } else if (min < 0 && sec < 0) {
      return `-${clockOutput(-min, -sec)}`; // NOTE: allow negative time display
    } else if (min <= 0 && sec <= 0) {
      return `-${clockOutput(-min, -sec)}`;
    } else {
      return clockOutput(min, sec);
    }
  } else {
    return (min <= 0 && sec <= 0) ? 'MISSED' : clockOutput(min, sec);
  }
}

export { displayCountdown };
