
import { helpers } from './helpers';
import { config } from './config';
import { arrivalsStorage } from './storage/arrivals-storage';
import { storageBase } from './storage/storage-base';
import { fetchTrainArrivalData } from './apis/arrivals-data';
import { fetchRouteData } from './apis/route-data';
import { fetchAlertData } from './apis/alert-data';
import { followersData } from './apis/followers-data';
import { followersStorage } from './storage/followers-storage';
import { settingsStorage } from './storage/settings-storage';
import { alertsStorage } from './storage/alerts-storage';
import { arrivalsHelper } from './views/arrivals-helper';
import { timeHelper } from './views/time-helper';

const { commutes } = config;
const { CtaStorage } = storageBase;


// NOTE: global helpers can go here, should be extracted of course soon
function isEmpty(obj) {
  if (Object.keys(obj).length > 0) {
    return false;
  }
  return true;
}

const ctaCommuteOptimizer = function() {
  let printAm;
  let printPm;

  $().ready(function() {
    $(commutes).each(function(i, amPm) {
      if (storageBase.storageEmpty() || arrivalsStorage.currentCommuteTypeNotSet(amPm)) {
        helpers.hideLoader();
      }
      const stationData = arrivalsStorage.fetchStationDataFor(amPm);
      const destinationData = arrivalsStorage.fetchDestinationStationDataFor(amPm);

      if (!isEmpty(stationData)) {
        $(`h2.${amPm}-commute-station-label`).text(`${stationData.stopName} to: `);
      }
      if (!isEmpty(destinationData)) {
        $(`h2.${amPm}-destination-station-label`).text(arrivalsStorage.fetchDestinationStationDataFor(amPm).stationName);
        $(`h2.${amPm}-destination-station-label`).removeClass('hidden');
        $('.station-setting.destination').removeClass('hidden');
      }
    });

    // https://superfish.joelbirch.co/options/
    $('#settings-menu').superfish({ delay: 100, speed: 'fast' });

    // SETTINGS START
    // CHECKBOX START
    $('#trainHeading').prop('checked', settingsStorage.getSetting('trainHeading'));
    $('#trainHeading').on('change', function () {
      settingsStorage.updateSetting('trainHeading', this.checked === true);
      $('canvas.compass').toggleClass('hidden');
    });
    $('#trainConfidence').prop('checked', settingsStorage.getSetting('trainConfidence'));
    $('#trainConfidence').on('change', function () {
      settingsStorage.updateSetting('trainConfidence', this.checked === true);
      $('span.confidence').toggleClass('hidden');
    });
    $('#predictionDiff').prop('checked', settingsStorage.getSetting('predictionDiff'));
    $('#predictionDiff').on('change', function () {
      settingsStorage.updateSetting('predictionDiff', this.checked === true);
      $('span.diff').toggleClass('hidden');
    });
    $('#trainRunNumber').prop('checked', settingsStorage.getSetting('trainRunNumber'));
    $('#trainRunNumber').on('change', function () {
      settingsStorage.updateSetting('trainRunNumber', this.checked === true);
      $('span.train-run-number').toggleClass('hidden');
    });
    $('#routeName').prop('checked', settingsStorage.getSetting('routeName'));
    $('#routeName').on('change', function () {
      settingsStorage.updateSetting('routeName', this.checked === true);
      $('span.route-name').toggleClass('hidden');
    });
    $('#alertDetails').prop('checked', settingsStorage.getSetting('alertDetails'));
    $('#alertDetails').on('change', function () {
      settingsStorage.updateSetting('alertDetails', this.checked === true);
      $('.event-details').toggleClass('hidden');
    });
    $('#accessibilitySetting').prop('checked', settingsStorage.getSetting('accessibilitySetting'));
    $('#accessibilitySetting').on('change', function () {
      settingsStorage.updateSetting('accessibilitySetting', this.checked === true);
      helpers.reload(); // NOTE: needed to get the data
    });

    // CHECKBOX END

    // SELECT DROPDOWN START
    // NOTE: arrival time pad change dropdown
    $('select.settings.alert-threshold').val(settingsStorage.getSetting('alertThreshold'));
    $('select.settings.alert-threshold').on('change', function () {
      settingsStorage.updateSetting('alertThreshold', parseInt(this.value, 10));

      $('li.alertbox').each(function(i, alertDiv) {
        if ($(alertDiv).data().sev > alertsStorage.alertSeverityThreshold()) {
          $(alertDiv).removeClass('hidden');
        } else {
          $(alertDiv).addClass('hidden');
        }
      });
      $(['station-alerts-am', 'station-alerts-pm', 'system-alerts', 'system-overall']).each(function(i, classToSet) {
        if ($(`.${classToSet}`).find('li.alertbox').not('.hidden').length > 0) {
          $(`.${classToSet}.hidden`).removeClass('hidden');
        } else {
          $(`.${classToSet}`).addClass('hidden');
        }
      });
    });

    $('select.settings.arrival-time-pad').val(settingsStorage.fetchSettingsObj().padArrivals);
    $('select.settings.arrival-time-pad').on('change', function () {
      settingsStorage.updateSetting('padArrivals', parseInt(this.value, 10));
      helpers.reload();
    });
    // SELECT DROPDOWN END

    $('span.settings.reset-muted-alerts').off();
    $('span.settings.reset-muted-alerts').click(function() {
      CtaStorage.removeItem(alertsStorage.MUTED_ALERTS_KEY);
      helpers.reload();
    });

    $('span.settings.reset-commute').off();
    $('span.settings.reset-commute').click(function() {
      CtaStorage.clear();
      helpers.reload();
    });

    // NOTE: jquery modal doc: https://jquerymodal.com/
    $('span.ride-history-trigger').off();
    $('span.ride-history-trigger').click(function() {
      const rides = followersStorage.rideHistory().rides;
      const toAppend = [];
      $('ul.ride-history-list').empty();

      if (rides.length === 0) {
        $('ul.ride-history-list').append('<li>No ride data. Go ride some trains!</li>');
      } else {
        $.each(rides, function(i, ride) {
          toAppend.push(`<li class='ride-history ride-history-${i}'>${ride.lineName} #${ride.runNum} to ${ride.destinationName} &#8212; <span title='Boarded at'>${timeHelper.printPlainDate(ride.boardedAt)} - ${timeHelper.printPlainTime(ride.boardedAt)}</span> <a class='ride-delete' data-ride-delete=${i} href='#'>Delete</a></li>`);
        });
        $('ul.ride-history-list').append(toAppend.join(''));
        $('.ride-delete').click(function() {
          const toDeleteIndex = $(this).data('rideDelete');
          followersStorage.deleteByIndex(toDeleteIndex);
          $(this).parent().remove();
        });
      }
    });
    // END SETTINGS

    // AM PM TOGGLE
    $('.switch-horizontal').off();
    $('.switch-horizontal').click(function(e) {
      const targetCommute = e.target.id.split('-')[1];

      // NOTE: only if doesn't have opposite commute data
      if ($(`ul.trainData-${targetCommute}`).find('li').length === 0) {
        helpers.showLoader();
      }
    });

    // ROUTE / LINE all or filter
    $(commutes).each(function(i, amPm) {
      $(`select.settings.${amPm}-train-filter`).on('change', function () {
        const routeId = this.value;
        settingsStorage.updateSetting(`${amPm}LineDisplay`, routeId);
        settingsStorage.handleShowHideRoutes(amPm, routeId);
        arrivalsHelper.handleShowHideArrivals(amPm, routeId);
      });

      $(`#radio-${amPm}`).off();
      $(`#radio-${amPm}`).click(function(e) {
        const opposite = helpers.selectedCommute();
        printPage({ forceAmPmVal: opposite });
      });
    });
    // END ROUTE / LINE all or filter
  });

  function getStationOpts(amPm) {
    return { stationid: arrivalsStorage.fetchStationDataFor(amPm).stationid, mapId: arrivalsStorage.fetchStationDataFor(amPm).mapId };
  }

  function printPage(opts = { forceAmPmVal: undefined }) {
    let stationOpts;

    if (!opts.forceAmPmVal === undefined || opts.forceAmPmVal) {
      printAm = opts.forceAmPmVal === 'am';
      printPm = opts.forceAmPmVal === 'pm';

      $(`div.all-data-${opts.forceAmPmVal}`).removeClass('hidden');
      $(`div.all-data-${helpers.oppositeCommute(opts.forceAmPmVal)}`).addClass('hidden');

      // NOTE: if has already fetched content hide/show and return
      if ($(`ul.trainData-${opts.forceAmPmVal}`).find('li').length > 0) {
        return;
      }
      stationOpts = getStationOpts(opts.forceAmPmVal);
      fetchTrainArrivalData(opts.forceAmPmVal, stationOpts);
    }

    // NOTE: is base and fallback, whole function should get cleaned up
    if (opts.forceAmPmVal === undefined) {
      const amPm = helpers.getCommuteType();
      printAm = amPm === 'am';
      printPm = amPm === 'pm';
      $(`#radio-${amPm}`).prop('checked', true);
      stationOpts = getStationOpts(amPm);

      fetchTrainArrivalData(amPm, stationOpts);
    }
    fetchRouteData({
      printAm: printAm,
      printPm: printPm,
      printSys: true,
    });
    fetchAlertData({
      printAm: printAm,
      printPm: printPm,
      printSys: true,
    });
  }
  printPage();
  setTimeout(function () {
    helpers.setInputFocus();
    followersData.hasRecentRide();
  }, 20);
};

export { ctaCommuteOptimizer };
