const timePrintOptions = {
  hour: 'numeric',
  minute: 'numeric',
  hour12: true,
};

const timeHelper = {
  parseCtaTime: function(ctaTimestamp) {
    const dateSplit = ctaTimestamp.split('T');
    const dayArr = dateSplit[0].split('-');
    const timeArr = dateSplit[1].split(':');
    return new Date(dayArr[0], (dayArr[1] - 1), dayArr[2], timeArr[0], timeArr[1], timeArr[2]);
  },

  printDate: function(ctaTimestamp) {
    // NOTE: docs: https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Date/toLocaleDateString
    const isToday = this.parseCtaTime(ctaTimestamp).setHours(0, 0, 0, 0) === new Date().setHours(0, 0, 0, 0);
    return isToday === true ? 'Today' : this.parseCtaTime(ctaTimestamp).toLocaleDateString(navigator.language);
  },

  printPlainDate: function(unix) {
    const date = new Date(unix);
    const month = (`0${(date.getMonth() + 1)}`).slice(-2);
    const day = (`0${date.getDate()}`).slice(-2);
    const year = date.getFullYear().toString().substr(-2);
    return [month, day, year].join('/');
  },

  printTime: function (ctaTimestamp) {
    return this.parseCtaTime(ctaTimestamp).toLocaleTimeString(navigator.language, timePrintOptions).replace(/(:\d{2})$/, '');
  },

  printPlainTime: function(unix) {
    return new Date(unix).toLocaleTimeString(navigator.language, timePrintOptions).replace(/(:\d{2})$/, '');
  },

  calcPercentChange: function(time1, time2) {
    // NOTE: is often abs but don't want here
    return ((this.getTimeDiff(time1, time2) / time1.getTime()) * 100 * 60 * 1000 * 100).toFixed(2);
  },

  getTimeDiff: function(time1, time2) {
    return time1.getTime() - time2.getTime();
  },
};

export { timeHelper };
