import { config } from './config';

const helpers = {
  getCommuteType: function() {
    const hours = new Date().getHours();
    // NOTE must change this if you're developing outside time window
    // TODO: read this from new settings when AM and PM commute start
    return (hours > 4 && hours < 12) ? 'am' : 'pm';
  },

  now: function() {
    return new Date().getTime();
  },

  reload: function (opts = { delay: config.reloadDefaultWait }) {
    $('.overlay-wrapper').addClass('overlay');

    function call() {
      return window.location.reload();
    }
    if (opts && opts.delay) {
      this.showLoader();

      setTimeout(function () {
        call();
      }, opts.delay);
    } else {
      call();
    }
  },

  // NOTE: not currently in use
  hidePageContent: function() {
    $('.all-data-am').addClass('hidden');
    $('.all-data-pm').addClass('hidden');
    $('.all-system-alerts').addClass('hidden');
  },

  showLoader: function() {
    $('img.loading-gif').removeClass('hidden');
  },

  hideLoader: function() {
    $('img.loading-gif').addClass('hidden');
  },

  showFooter: function() {
    $('footer').removeClass('hidden');
  },

  selectedCommute: function() {
    return $("#radio-pm[name='am-pm']")[0].checked === true ? 'pm' : 'am';
  },

  setInputFocus: function() {
    if ($('#am-station-search').is(':visible')) {
      $('input#am-station-search').focus();
    } else if ($('#pm-station-search').is(':visible')) {
      $('input#pm-station-search').focus();
    }
  },

  oppositeCommute: function(amPm) {
    return amPm.toLowerCase() === 'am' ? 'pm' : 'am';
  },

};

export { helpers };
