import { alertsStorage } from '../storage/alerts-storage';
import { settingsStorage } from '../storage/settings-storage';
import { timeHelper } from './time-helper';

// NOTE: uses severityScores object
function formatEventTime(ctaTimestamp) {
  return ctaTimestamp === null ? 'TBD' : `${timeHelper.printTime(ctaTimestamp)} ${timeHelper.printDate(ctaTimestamp)}`;
}

const alertsHelper = {
  alertMuteClass: function(alertInfo) {
    return alertsStorage.shouldDisplayAlert(alertInfo.AlertId, alertInfo.SeverityScore) === true ? '' : 'hidden';
  },

  printEventDetails: function(alertInfo) {
    return `<p class='event-details hidden'>${formatEventTime(alertInfo.EventStart)} &#8212; ${formatEventTime(alertInfo.EventEnd)}</p>`;
  },

  readAlertDetailsSetting: function() {
    if (settingsStorage.getSetting('alertDetails') === true) {
      $('.event-details').removeClass('hidden');
    } else {
      $('.event-details').addClass('hidden');
    }
  },
};

export { alertsHelper };
