import { storageBase } from './storage-base';
import { helpers } from '../helpers';

const CURRENT_RIDE_KEY = 'cta-current-ride';
const RIDE_HISTORY_KEY = 'cta-ride-history';
const { CtaStorage } = storageBase;

const followersStorage = {
  addRideToHistory: function(ride) {
    const history = this.rideHistory();
    history.rides.push(ride);
    CtaStorage.setItem(RIDE_HISTORY_KEY, JSON.stringify(history));
  },

  rideHistory: function() {
    return JSON.parse(CtaStorage.getItem(RIDE_HISTORY_KEY)) || {
      rides: [],
      updatedAt: helpers.now(),
    };
  },

  // NOTE: doesn't create, only fetches
  currentRide: function() {
    return JSON.parse(CtaStorage.getItem(CURRENT_RIDE_KEY));
  },
  deleteByIndex: function(i) {
    const history = this.rideHistory();
    if (i > -1) {
      history.rides.splice(i, 1);
    }
    CtaStorage.setItem(RIDE_HISTORY_KEY, JSON.stringify(history));
  },

  lastEtaRaw: function() {
    if (this.currentRide()) {
      return this.currentRide().lastEtaRaw;
    }
    return 'unknown';
  },

  hasCurrentRide: function() {
    return this.currentRide() !== null;
  },

  // NOTE: rides over an hour are considered stale
  currentRideStale: function() {
    return helpers.now() - this.currentRide().boardedAt > 60 * 60 * 1000;
  },

  arrived: function() {
    if (this.currentRide()) {
      return this.currentRide().lastEta === 'ARRIVED';
    }
    return false;
  },

  clearCurrentRideKey: function() {
    CtaStorage.removeItem(CURRENT_RIDE_KEY);
  },

  updateCurrentRide: function (key, value) {
    const currentRide = this.currentRide();
    currentRide.updatedAt = helpers.now();
    currentRide[key] = value;
    CtaStorage.setItem(CURRENT_RIDE_KEY, JSON.stringify(currentRide));
  },

  // NOTE: is only called when a train is boarded
  setCurrentRide: function(opts = {}) {
    this.clearCurrentRideKey();
    const currentRide = {};
    currentRide.boardedAt = opts.boardedAt;
    currentRide.destinationStation = opts.destinationStation;
    currentRide.lineName = opts.lineName;
    currentRide.runNum = opts.runNum;
    currentRide.destinationName = opts.destinationName;
    currentRide.updatedAt = helpers.now();
    currentRide.lastEta = 'unknown'; // NOTE not known yet
    currentRide.lastEtaRaw = 'unknown';
    // NOTE: don't include all the run data
    const history = currentRide;
    this.addRideToHistory(history);
    currentRide.runData = opts.runData;
    CtaStorage.setItem(CURRENT_RIDE_KEY, JSON.stringify(currentRide));
  },
};

export { followersStorage };
