// NOTE: signficant is defined default as 5%
function diffColor(percentChange) {
  if (percentChange === 0) {
    return '#ddd';
  }
  if (percentChange < -29) {
    return '#e60000';
  }
  if (percentChange < 5) {
    return '#e8564e';
  }
  if (percentChange > 29) {
    return '#60ed44';
  }
  if (percentChange > 5) {
    return '#9eff9e';
  }
  return '#ddd';
}

// NOTE: significant is defined default as 30 seconds
function formatDiff(difference) {
  const diff = parseFloat(difference);
  if (diff === 0.0) {
    return `${diff}`;
  }
  if (diff > 29) {
    return `++${diff}`;
  }
  if (diff > 0) {
    return `+${diff}`;
  }
  if (diff < -29) {
    return `-${diff}`;
  }
  if (diff < 0) {
    return `${diff}`;
  }
}

export { diffColor, formatDiff };
