import { config } from '../config';
import { helpers } from '../helpers';
import { arrivalsStorage } from '../storage/arrivals-storage';
import { timeHelper } from '../views/time-helper';
import { diffColor, formatDiff } from '../views/time-diff';
import { getColorForPercentage } from '../views/color-percentage';
import { settingsStorage } from '../storage/settings-storage';
import { arrivalsHelper } from '../views/arrivals-helper';
import { displayCountdown } from '../views/countdown-timer';
import { setCompass } from '../views/compass';

const {
  colorToObjMap,
  proxyUrl,
  apiKey,
  timerInterval,
} = config;

function stationQueryUrl(stationid) {
  return `${proxyUrl}https://lapi.transitchicago.com/api/1.0/ttarrivals.aspx?key=${apiKey}&mapid=${stationid}&outputType=JSON`;
}

function getDiffToPrint(diff, percentChange) {
  return (`<span title='diff since last prediction' class='diff' style='color: ${diffColor(percentChange)}'> ${formatDiff(diff)}</span>`);
}

function printArrivals(desiredTrains, amPm) {
  let toAppend;
  if (desiredTrains.length === 0) {
    toAppend = "<li class='arrival-time' style='background-color: #e60000;'><div class='line-detail'></div><div class='station-arrival'><span title='Station Name'>No Train Predictions Available</span></div></li>";

    $(`ul.trainData-${amPm}`).append(toAppend);
    showDataSectionFor(amPm);
  } else {
    $(desiredTrains).each(function (i, train) {
      let confidenceLevel;
      const lastPredictedArrival = arrivalsStorage.getArrivalPredictions(train, amPm);
      const arrivalTime = timeHelper.parseCtaTime(train.arrT);

      // NOTE: to read about prediction, go to:
      // https://www.transitchicago.com/developers/ttdocs/
      // see section on: 'Calculating a number of minutes until arrival from this data'
      const predictionTime = timeHelper.parseCtaTime(train.prdt);
      const predictDurationMinutes = (helpers.now() - predictionTime) / 60 / 1000;
      const predictionConfidenceColor = getColorForPercentage(1 - (predictDurationMinutes / 2));
      const paddingArrivalsBy = settingsStorage.getSetting('padArrivals'); // NOTE: seconds
      let startingTimeToDepartMs = (arrivalTime - helpers.now() - (paddingArrivalsBy * 1000));
      let diffToPrint = '';
      const timerClass = `countdown-${i}-${amPm}`;
      const confidenceClass = `confidence-${i}-${amPm}`;
      const airportIcon = arrivalsHelper.airportIcon(train);

      if (lastPredictedArrival === undefined) {
        diffToPrint = "<span class='diff'> 0</span>";
      } else {
        const parsedLastPredictedArrival = timeHelper.parseCtaTime(lastPredictedArrival);
        const timeDiff = timeHelper.getTimeDiff(arrivalTime, parsedLastPredictedArrival) / 1000; // Seconds
        const percentChange = timeHelper.calcPercentChange(parsedLastPredictedArrival, arrivalTime);
        diffToPrint = getDiffToPrint(timeDiff, percentChange);
      }

      toAppend = `<li class='arrival-time filter-${train.rt}' style='background-color: #${arrivalsHelper.trainColor(train)};'><div class='line-detail' data-line-name='${colorToObjMap[train.rt].name}' data-run-number='${train.rn}'><span title='L Train Color' class='route-name'>${arrivalsHelper.routeName(train)}</span><span title='Run Number' class='train-run-number'> #${train.rn}</span></div><div class='station-arrival'><span title='Station Name'>${train.destNm}</span>${airportIcon}<canvas title='Train Compass Heading' class='compass' height='50' width='50'></canvas><span title='confidence rating' class='confidence ${confidenceClass}' style='color: ${predictionConfidenceColor};'> ${predictDurationMinutes.toFixed(2)}</span>${diffToPrint}<span title='delayed flag' style='color: red;'> ${arrivalsHelper.delayedText(train)}</span><span title='time until arrival' class='countdown ${timerClass}'></span></div></li>`;
      $(`ul.trainData-${amPm}`).append(toAppend);

      setInterval((function () {
        function timer() {
          const intervalTimeToDepart = startingTimeToDepartMs -= timerInterval;
          const min = Math.floor((intervalTimeToDepart) / 60000);
          const sec = ((intervalTimeToDepart % 60000) / 1000).toFixed(0);
          const isDelay = arrivalsHelper.isDelayed(train);
          $(`.${timerClass}`).text(displayCountdown(isDelay, min, sec));
          $(`.${confidenceClass}`).text(confidenceLevel);
          // TODO revamp opacity and maybe use
          // $('.' + timerClass).parent().css('opacity', setOpacity(amPm, timeToDepartMS));
        }
        timer();
        return timer;
      }()), timerInterval);

      const target = $(`ul.trainData-${amPm}`).find('li').last()[0];
      setCompass(train.heading, target);
      helpers.hideLoader();
      arrivalsHelper.showTrainFilterFor(amPm);
      arrivalsHelper.showDataSectionFor(amPm);
      arrivalsHelper.handleShowHideArrivals(amPm, settingsStorage.lineFilterSetting(amPm));
    });
    arrivalsHelper.setRideListeners();
  }
}

function fetchTrainArrivalData(amPm, opts) {
  if (opts.stationid === undefined) {
    return;
  }

  $.ajax({
    url: stationQueryUrl(opts.stationid),
    type: 'GET',
    async: true,
  }).done(function (response) {
    const trainEtas = response.ctatt.eta;
    const desiredTrains = [];

    $(trainEtas).each(function (i, train) {
      if (parseInt(train.stpId, 10) === opts.mapId) {
        desiredTrains.push(train);
      }
    });

    helpers.showFooter();
    helpers.hideLoader();
    arrivalsStorage.clearOldTrainArrivalData();
    printArrivals(desiredTrains, amPm);

    // NOTE: need to handle settings here as page isn't ready until here
    if (settingsStorage.settingOff('trainHeading')) {
      $('canvas.compass').addClass('hidden');
    }
    if (settingsStorage.settingOff('trainConfidence')) {
      $('span.confidence').addClass('hidden');
    }
    if (settingsStorage.settingOff('predictionDiff')) {
      $('span.diff').addClass('hidden');
    }
    if (settingsStorage.settingOff('trainRunNumber')) {
      $('span.train-run-number').addClass('hidden');
    }
    if (settingsStorage.settingOff('routeName')) {
      $('span.route-name').addClass('hidden');
    }
  }).fail(function (data, textStatus, xhr) {
    // NOTE: API error codes:
    // https://www.transitchicago.com/developers/ttdocs/#_Toc296199912

    console.log(data);
    console.log(textStatus);
    console.log(xhr);
    alert('train arrival endpoint failed, check console.log');
  });
}

export { fetchTrainArrivalData };
