// https://stackoverflow.com/questions/36236814/wind-direction-on-a-compass-wunderground/36242222

function setCompass(deg, target) {
  const degrees = parseInt(deg, 10);
  const ctx = $(target).find('canvas.compass')[0].getContext('2d');
  const radians = 0.0174533 * (degrees - 90);
  // NOTE: calc compass centre
  const x = ctx.canvas.width / 2;
  const y = ctx.canvas.height / 2;
  const r = x * 0.8;
  const pointerX = x + r * Math.cos(radians);
  const pointerY = y + r * Math.sin(radians);
  const gaugeSize = x - 2;

  // NOTE: clear
  ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
  ctx.beginPath();

  // NOTE: styling
  ctx.strokeStyle = 'rgba(0,0,0,0.25)';
  ctx.lineWidth = 1;
  ctx.fillStyle = 'rgba(255,255,255,0.9)';
  ctx.arc(x, y, gaugeSize, 0, 2 * Math.PI); // 2 * Math.PI
  ctx.fill();
  ctx.stroke();

  // NOTE: draw compass needle
  ctx.beginPath();
  ctx.strokeStyle = 'rgba(255,0,0,0.5)';
  ctx.lineCap = 'round';
  ctx.moveTo(x, y);
  ctx.lineTo(pointerX, pointerY);
  ctx.moveTo(pointerX, pointerY);
  ctx.lineWidth = 2;

  ctx.fillStyle = '#999';
  // ctx.fillText(degrees, x, y);
  ctx.stroke();
}

export { setCompass };
