import { storageBase } from './storage-base';
import { helpers } from '../helpers';

const arrivalPredictionNamespace = 'ctaPredict';
const { CtaStorage } = storageBase;

function staleUpdatedAt(updatedAt) {
  return (helpers.now() - updatedAt) > keyExpiry();
}

function keyExpiry() {
  return 60 * 1000 * 5;
}

function arrivalPredictionKeyFor(train, amPm) {
  return `${arrivalPredictionNamespace}-${train.rt}-${train.rn}-${train.destNm}-${amPm}`;
}

function ctaArrivalKey(keyName) {
  return keyName.split('-')[0] === arrivalPredictionNamespace;
}
function getUpdatedAt(keyName) {
  return JSON.parse(CtaStorage.getItem(keyName)).updatedAt;
}

const arrivalsStorage = {
  currentCommuteTypeNotSet: function(amPm) {
    return CtaStorage.getItem(`${helpers.oppositeCommute(amPm)}-commute-station`) === null;
  },

  fetchStationDataFor: function(amPm) {
    const commuteData = CtaStorage.getItem(`${amPm}-commute-station`);
    if (commuteData === null) {
      return {};
    }
    return JSON.parse(commuteData);
  },

  fetchDestinationStationDataFor: function (amPm) {
    const commuteData = CtaStorage.getItem(`${amPm}-destination-station`);
    if (commuteData === null) {
      return {};
    }
    return JSON.parse(commuteData);
  },

  clearOldTrainArrivalData: function() {
    storageBase.allKeys().forEach(function (key) {
      if (!key.match(/ctaPredict/)) {
        return;
      }
      if (ctaArrivalKey(key) && staleUpdatedAt(getUpdatedAt(key))) {
        CtaStorage.removeItem(key);
      }
    });
  },

  getArrivalPredictions: function(train, amPm) {
    const key = arrivalPredictionKeyFor(train, amPm);
    const item = JSON.parse(CtaStorage.getItem(key)) || {
      values: [],
      updatedAt: null,
    };
    item.values.push(train.arrT);
    item.updatedAt = helpers.now();
    CtaStorage.setItem(key, JSON.stringify(item));
    // NOTE: get 2nd to last stored value, order matters
    return item.values[item.values.length - 2];
  },
};

export { arrivalsStorage };
