import { arrivalsStorage } from '../storage/arrivals-storage';
import { settingsStorage } from '../storage/settings-storage';
import { config } from '../config';
import { helpers } from '../helpers';

const { colorToObjMap, proxyUrl, colorToRouteMap } = config;
const routeStatusUrl = 'https://lapi.transitchicago.com/api/1.0/routes.aspx?outputType=JSON';

// &type=rail,station,systemwide
function generateRouteStatusUrl(forObject) {
  return proxyUrl + routeStatusUrl + forObject;
}

function setRouteData(urlFor, classToSet, forObject, amPm, stationid) {
  if (!['station', 'system'].includes(forObject)) {
    throw 'Unknown type passed to: `forObject`!';
  }

  if (stationid === undefined) {
    return;
  }

  $.ajax({
    url: urlFor,
    type: 'GET',
    async: true,
  }).done(function (response) {
    let routeInfos;
    // NOTE: this odd, sometimes returns a single object, not an array
    if (Array.isArray(response.CTARoutes.RouteInfo)) {
      routeInfos = response.CTARoutes.RouteInfo;

      // USED TO BE:  && Object.keys(response.CTARoutes.RouteInfo).length < 2
    } else if (response.CTARoutes.RouteInfo) {
      routeInfos = [response.CTARoutes.RouteInfo];
    }

    const colorsToPrint = arrivalsStorage.fetchStationDataFor(helpers.selectedCommute()).activeLines;
    // NOTE: set train route filter dropdown
    $.each(colorsToPrint, function (key, value) {
      $(`select.settings.${amPm}-train-filter`).append($(`<option value='${value}'>${colorToObjMap[value].name}</option>`));
    });

    $(routeInfos).each(function (i, routeInfo) {
      let toAppend;
      let counter = 0;
      if (forObject === 'station') {
        counter += 1;
        // NOTE:
        // this is a weird way to do this, but is all the api returns
        // would be nice to get routeid as well
        const routeColor = colorToRouteMap[routeInfo.RouteColorCode.toLowerCase()];
        const routeObj = colorToObjMap[routeColor];

        if (routeColor === undefined || routeObj === undefined) {
          throw 'Look at keys in colorToRouteMap or colorToObjMap, a key is likely missing';
        }

        if (colorsToPrint.includes(colorToObjMap[routeColor].routeId)) {
          toAppend = `<li class='route-data filter-${routeObj.routeId}'><img class='train-icon' src='${routeObj.icon}' height='80' width='80' /><b><a target='_blank' href='${routeObj.routeUrl}' style='color: #${routeInfo.RouteColorCode};'>${routeInfo.RouteStatus}</b></li>`;
        }
      } else if (forObject === 'system') {
        if (routeInfo.RouteColorCode !== '000000' && routeInfo.RouteColorCode !== null) {
          counter += 1;
          $('.system-overall.hidden').removeClass('hidden');
          toAppend = `<li class='routedata'><b style='color: #${routeInfo.RouteColorCode};'>${routeInfo.Route} - ${routeInfo.RouteStatus}</b></li>`;
        }
      }
      if (counter > 0) {
        $(`ul.${classToSet}`).append(toAppend);
      }
    });
    // And set dropdown to your persisted settings
    const routeId = settingsStorage.lineFilterSetting(amPm);
    $(`select.settings.${amPm}-train-filter`).val(routeId);
    settingsStorage.handleShowHideRoutes(amPm, routeId);
  }).fail(function (data, textStatus, xhr) {
    console.log(data);
    console.log(textStatus);
    console.log(xhr);
  });
}

function fetchRouteData(obj) {
  let url;
  let stationid;
  const printAm = obj.printAm;
  const printPm = obj.printPm;
  const printSys = obj.printSys;

  if (printAm) {
    // NOTE: do not make call if it is undefined
    stationid = arrivalsStorage.fetchStationDataFor('am').stationid;
    if (stationid === undefined) {
      return;
    }
    url = generateRouteStatusUrl(`&stationid=${stationid}`);
    setRouteData(url, 'routedata-am', 'station', 'am', stationid);
  }

  if (printPm) {
    // NOTE: do not make call if it is undefined
    stationid = arrivalsStorage.fetchStationDataFor('pm').stationid;
    if (stationid === undefined) {
      return;
    }
    url = generateRouteStatusUrl(`&stationid=${stationid}`);
    setRouteData(url, 'routedata-pm', 'station', 'pm', stationid);
  }

  if (printSys) {
    url = generateRouteStatusUrl('&type=systemwide');
    setRouteData(url, 'system-overall', 'system', stationid);
  }
}

export { fetchRouteData };
