// APP SETTINGS
// TODO: make an actual settings object with functions that
// when saved updates timestamp etc
// and cleanup JSON.parse in main.js

import { storageBase } from './storage-base';
import { helpers } from '../helpers';

const { CtaStorage } = storageBase;
const { now } = helpers;

const settingsStorage = {
  SETTINGS_KEY: 'cta-settings',

  fetchSettingsObj: function() {
    return JSON.parse(CtaStorage.getItem(this.SETTINGS_KEY)) || {
      accessibilitySetting: false,
      alertThreshold: 2, // NOTE: 0 is 'all'
      alertDetails: false,
      padArrivals: 0,
      amLineDisplay: 'all',
      pmLineDisplay: 'all',
      predictionDiff: true,
      routeName: true,
      trainConfidence: true,
      trainHeading: true,
      trainRunNumber: true,
      updatedAt: now(),
    };
  },

  lineFilterSetting: function(amPm) {
    return this.fetchSettingsObj()[`${amPm}LineDisplay`];
  },

  // NOTE: force route for easier color handling
  // could also add Pexp in the class on the arrival-time <li>
  inclusivePurpleClassKey: function(lineFilterSetting) {
    return lineFilterSetting === 'Pexp' ? 'P,Pexp' : lineFilterSetting;
  },

  filterSelection: function(amPm) {
    return this.inclusivePurpleClassKey(this.lineFilterSetting(amPm));
  },

  clearFilterCache: function(amPm) {
    this.updateSetting(`${amPm}LineDisplay`, 'all');
  },

  settingOff: function(key) {
    return !this.getSetting(key);
  },

  getSetting: function(key) {
    return this.fetchSettingsObj()[key];
  },

  updateSetting: function(key, value) {
    const settingsObj = this.fetchSettingsObj();
    settingsObj.updatedAt = now();
    settingsObj[key] = value;
    CtaStorage.setItem(this.SETTINGS_KEY, JSON.stringify(settingsObj));
  },

  hideShowRoutes: function(amPm) {
    // NOTE: hide route status for non matching trains
    const filterSetting = this.filterSelection(amPm);
    $(`ul.routedata-${amPm}`).find($(`.route-data:not(.filter-${filterSetting})`)).addClass('hidden');
    $(`ul.routedata-${amPm}`).find($(`.filter-${filterSetting}`)).removeClass('hidden');
  },

  showAllRoutes: function(amPm) {
    $(`ul.routedata-${amPm}`).find($('.route-data')).removeClass('hidden');
  },

  handleShowHideRoutes: function (amPm, routeId) {
    return routeId === 'all' ? this.showAllRoutes(amPm) : this.hideShowRoutes(amPm);
  },

};

export { settingsStorage };
