const followersHelper = {
  setArrived: function() {
    $('.boarded-wrapper').removeClass('animate-boarded');
    $('.boarded-wrapper').addClass('boarded-arrived');
    $('.boarded-wrapper').removeClass('hidden');
  },

  showRideBanner: function() {
    $('.boarded-wrapper').removeClass('boarded-arrived');
    $('.boarded-wrapper').addClass('animate-boarded');
    $('.boarded-wrapper').removeClass('hidden');
  },
};

export { followersHelper };
