import { config } from '../config';
import { settingsStorage } from '../storage/settings-storage';
import { followersData } from '../apis/followers-data';

const { colorToObjMap } = config;
const airportIcon = require('../../images/airport-icon.png');

function showAllArrivals(amPm) {
  $(`ul.trainData-${amPm}`).find($('.arrival-time')).removeClass('hidden');
}

function hideShowArrivals(amPm) {
  // NOTE: remove arrival times for non matching trains
  const filterSetting = settingsStorage.filterSelection(amPm);
  $(`ul.trainData-${amPm}`).find($(`.arrival-time:not(.filter-${filterSetting})`)).addClass('hidden');
  $(`ul.trainData-${amPm}`).find($(`.filter-${filterSetting}`)).removeClass('hidden');
}

const arrivalsHelper = {
  showDataSectionFor: function(amPm) {
    $(`.all-data-${amPm}`).removeClass('hidden');
  },

  showTrainFilterFor: function(amPm) {
    $(`.settings.${amPm}-train-filter.hidden`).removeClass('hidden');
  },

  handleShowHideArrivals: function(amPm, routeId) {
    if (routeId === 'all') {
      showAllArrivals(amPm);
    } else {
      hideShowArrivals(amPm);
    }
  },

  setRideListeners: function() {
    $('.line-detail').off('click');
    $('.line-detail').on('click', function (e) {
      const data = $(e.target).parent().data();
      followersData.boardTrain(data.runNumber, data.lineName);
    });
  },

  routeName: function(train) {
    return colorToObjMap[train.rt].name;
  },

  trainColor: function(train) {
    return colorToObjMap[train.rt].color;
  },

  delayedText: function(train) {
    return train.isDly === '0' ? '' : 'DELAYED';
  },

  isDelayed: function(train) {
    return train.isDly === '1';
  },

  airportBound: function(train) {
    // NOTE: IFF is blue line and headed for ohare
    // NOTE: IFF is orange and headed for midway

    if (Object.keys(config.airportBoundLines).includes(train.rt)) {
      return parseInt(config.airportBoundLines[train.rt], 10) === parseInt(train.trDr, 10);
    }
    return false;
  },

  airportIcon: function(train) {
    if (this.airportBound(train)) {
      return `<img title='Airport Bound' class='airport-icon' src=${airportIcon} height='50' width='50' />`;
    }
    return '';
  },
};

export { arrivalsHelper };
