import { storageBase } from './storage-base';
import { settingsStorage } from './settings-storage';
import { helpers } from '../helpers';
import { severityScores } from '../data/severity-scores';

const { CtaStorage } = storageBase;
const MUTED_ALERTS_KEY = 'cta-muted-alerts';

function fetchMutedAlertsObj() {
  return JSON.parse(CtaStorage.getItem(MUTED_ALERTS_KEY)) || {
    updatedAt: helpers.now(),
    mutedAlerts: [],
  };
}

function alertIdMatches(id1, id2) {
  return parseInt(id1, 10) === parseInt(id2, 10);
}

function sevNotIncreased(sevOld, sevNew) {
  return parseInt(sevOld, 10) <= parseInt(sevNew, 10);
}


const alertsStorage = {
  MUTED_ALERTS_KEY: MUTED_ALERTS_KEY,
  alertObject: fetchMutedAlertsObj(),

  // TODO: write and where call
  // clearOldAlerts: function() {
  // },

  muteAlert: function(alertId, sev) {
    const alertObject = fetchMutedAlertsObj();
    alertObject.mutedAlerts.push(
      {
        alertId: alertId,
        sev: sev,
        updatedAt: helpers.now(),
      },
    );
    alertObject.updatedAt = helpers.now();
    CtaStorage.setItem(MUTED_ALERTS_KEY, JSON.stringify(alertObject));
  },

  alertSeverityThreshold: function() {
    return severityScores[settingsStorage.fetchSettingsObj().alertThreshold].lowSev;
  },

  // NOTE: checks if is hidden and sev didn't change significantly since mute
  shouldDisplayAlert: function(alertId, sev) {
    const that = this;
    that.alertId = alertId;
    that.sevNew = sev;
    that.shouldDisplay = true;
    const mutedAlerts = fetchMutedAlertsObj().mutedAlerts;

    mutedAlerts.forEach(function (mutedAlert) {
      if (alertIdMatches(mutedAlert.alertId, that.alertId) && sevNotIncreased(mutedAlert.sev, that.sevNew)) {
        that.shouldDisplay = false;
        return;
      }
    });
    return that.shouldDisplay;
  },
  // TODO: delete old alerts (after what? weeks?)
};

export { alertsStorage };
