const isProd = function() {
  return !!window.location.href.match(/github.io/);
};

const storageBase = {
  CtaStorage: (function() {
    if (isProd()) {
      return window.localStorage;
    } else {
      return window.sessionStorage;
    }
  }()),

  allKeys: function() {
    return Object.keys(this.CtaStorage);
  },

  storageEmpty: function() {
    return this.allKeys().length === 0;
  },

  clear: function() {
    this.CtaStorage.clear();
  },
};

export { storageBase };
