import { settingsStorage } from '../storage/settings-storage';
import { arrivalsStorage } from '../storage/arrivals-storage';
import { alertsStorage } from '../storage/alerts-storage';
import { alertsHelper } from '../views/alerts-helper';
import { config } from '../config';

const { proxyUrl } = config;
const closeIcon = require('../../images/close-icon-32.png');

const alertUrl = 'https://lapi.transitchicago.com/api/1.0/alerts.aspx?outputType=JSON';

function detailedAlertsUrl(query) {
  return proxyUrl + alertUrl + query;
}

// NOTE: should only be called once per page load
function setAlertData(urlFor, classToSet) {
  var alertArr = [];

  $.ajax({
    url: urlFor,
    type: 'GET',
    async: true,
  }).done(function (response) {
    var alerts = response.CTAAlerts.Alert;

    $(alerts).each(function (i, alertInfo) {
      var toAppend;
      var backgroundColor = alertInfo.SeverityColor;
      var hideClass = parseInt(alertInfo.SeverityScore, 10) > alertsStorage.alertSeverityThreshold() ? '' : 'hidden';
      if (backgroundColor === '000000') {
        backgroundColor = '999';
      }

      if (!alertArr.includes(alertInfo.AlertId)) {
        alertArr.push(alertInfo.AlertId);
        toAppend = `<li data-alert-id='${alertInfo.AlertId}' data-sev='${alertInfo.SeverityScore}' class='alertbox alertbox-${alertInfo.AlertId} ${hideClass} ${alertsHelper.alertMuteClass(alertInfo)}' style='background-color: #${backgroundColor};'>${alertsHelper.printEventDetails(alertInfo)}<a style='color: white;' href='${alertInfo.AlertURL['#cdata-section']} target='_blank'><b>${alertInfo.Headline}</b></a><span class='sev-score' style='float: right;'>(sev: ${alertInfo.SeverityScore})</span><img src='${closeIcon}' class='icon close-icon'/><p>${alertInfo.ShortDescription}</p></li>`;
      }
      $(`ul.${classToSet}`).append(toAppend);

      // NOTE: is not counter, but how many divs are not hidden
      // Controls printing of h2's
      if ($(`.${classToSet}`).find('li.alertbox').not('.hidden').length > 0) {
        $(`.${classToSet}.hidden`).removeClass('hidden');
      } else {
        $(`.${classToSet}`).addClass('hidden');
      }
    });

    $('img.close-icon').off('click');
    $('img.close-icon').click(function () {
      var alertId = $(this).parent().data().alertId;
      var sev = $(this).parent().data().sev;
      alertsStorage.muteAlert(alertId, sev);
      $(this).parent().remove();
    });
    alertsHelper.readAlertDetailsSetting();
  }).fail(function (data, textStatus, xhr) {
    console.log(data);
    console.log(textStatus);
    console.log(xhr);
  });
}

function fetchAlertData(obj) {
  var url;
  var showAccessibility = settingsStorage.getSetting('accessibilitySetting');
  var baseQuery = `&activeonly=true&accessibility=${showAccessibility}`;
  var printAm = obj.printAm;
  var printPm = obj.printPm;
  var printSys = obj.printSys;

  if (printAm) {
    // NOTE: do not make call if it is undefined
    var stationid = arrivalsStorage.fetchStationDataFor('am').stationid;
    if (stationid === undefined) {
      return;
    }
    url = detailedAlertsUrl(`${baseQuery}&stationid=${stationid}`);
    setAlertData(url, 'station-alerts-am');
  }

  if (printPm) {
    // NOTE: do not make call if it is undefined
    var stationid = arrivalsStorage.fetchStationDataFor('pm').stationid;
    if (stationid === undefined) {
      return;
    }
    url = detailedAlertsUrl(`${baseQuery}&stationid=${stationid}`);
    setAlertData(url, 'station-alerts-pm');
  }

  if (printSys) {
    var amData = arrivalsStorage.fetchStationDataFor('am').activeLines || [];
    var pmData = arrivalsStorage.fetchStationDataFor('pm').activeLines || [];
    var amPmRoutesMerged = amData.concat(pmData).join();

    if (amPmRoutesMerged === '') {
      return;
    }
    url = detailedAlertsUrl(`${baseQuery}&routeid=${amPmRoutesMerged}`);
    setAlertData(url, 'system-alerts');
  }
}

export { fetchAlertData };

