const percentColors = [
  { pct: 0.0, color: { r: 0xff, g: 0x00, b: 0 } },
  { pct: 0.5, color: { r: 0xff, g: 0xff, b: 0 } },
  { pct: 1.0, color: { r: 0x00, g: 0xff, b: 0 } },
];

function colorFloor(color) {
  if (color < 0) {
    return 0;
  } else if (color > 255) {
    return 255;
  } else {
    return color;
  }
}

// color slider from here:
// https://stackoverflow.com/questions/7128675/from-green-to-red-color-depend-on-percentage
function getColorForPercentage(pct) {
  let i;

  for (i = 1; i < percentColors.length - 1; i += 1) {
    if (pct < percentColors[i].pct) {
      break;
    }
  }
  const lower = percentColors[i - 1];
  const upper = percentColors[i];
  const range = upper.pct - lower.pct;
  const rangePct = (pct - lower.pct) / range;
  const pctLower = 1 - rangePct;
  const pctUpper = rangePct;
  const color = {
    r: colorFloor(Math.floor(lower.color.r * pctLower + upper.color.r * pctUpper)),
    g: colorFloor(Math.floor(lower.color.g * pctLower + upper.color.g * pctUpper)),
    b: colorFloor(Math.floor(lower.color.b * pctLower + upper.color.b * pctUpper)),
  };

  // or output as hex if preferred
  return `rgb(${[color.r, color.g, color.b].join(',')})`;
}

export { getColorForPercentage };
